        <h1 class="border-bottom">Home view</h1>
        <button @click="onTest">Test</button>
        <textarea cols="80" id="editor1" name="editor1"
            rows="10">&lt;p&gt;This is some &lt;strong&gt;sample text&lt;/strong&gt;. You are using &lt;a href=&quot;https://ckeditor.com/&quot;&gt;CKEditor&lt;/a&gt;.&lt;/p&gt;</textarea>

    </div>
