<script>

function loadScript(id, url) {
    return new Promise(function (resolve, reject) {
        var script = document.createElement("script");
        script.onload = resolve;
        script.onerror = reject;
        script.src = url;
        script.type = "text/javascript";
        script.setAttribute('id', id);
        document.getElementsByTagName("head")[0].appendChild(script);
    });
};
export default {
    components: {
    },
    mounted() {
        let that = this;
        let cke = "/js/ckeditor_4.21.0_full/ckeditor/ckeditor.js";
        //console.log('myscirpt_full_enhand1 : '+ document.getElementById("myscirpt_full_enhand1"));
        if (!document.getElementById("Basic")) {
            loadScript("Basic", cke).then(() => {
                console.log('not load');
                onReady(that);
            })
        } else {
            console.log('loaded');
            onReady(that)
        }
        function onReady(that) {
            var editor = CKEDITOR.replace('editor1', {
                extraPlugins: 'image2,uploadimage,timestamp',
                removePlugins: 'image,exportpdf',
                toolbarGroups: [
                    { name: 'clipboard', groups: ['clipboard', 'undo'] },
                    //{ name: 'editing', groups: ['find', 'selection', 'spellchecker', 'editing'] },
                    { name: 'document', groups: ['mode', 'document', 'doctools'] },
                    //{ name: 'forms', groups: ['forms'] },
                    { name: 'basicstyles', groups: ['basicstyles', 'cleanup'] },
                    //{ name: 'paragraph', groups: ['list', 'indent', 'blocks', 'align', 'bidi', 'paragraph'] },
                    //{ name: 'links', groups: ['links'] },
                    { name: 'insert', groups: ['insert'] },
                    { name: 'styles', groups: ['styles'] },
                    //{ name: 'colors', groups: ['colors'] },
                    //{ name: 'tools', groups: ['tools'] },
                    { name: 'others', groups: ['others'] },
                    //{ name: 'about', groups: ['about'] }
                ],
                format_tags: 'p;h1;h2;h3;pre',
                removeDialogTabs: 'image:advanced;link:advanced',
                height: 150,
                removeButtons: 'PasteFromWord',
            });
            /* Add button */
            editor.addCommand("mySimpleCommand", { // create named command
                exec: function (edt) {
                    //alert(edt.getData());
                    // that.sel = '';
                    //that.showModal();
                    that.onTest();
                }
            });
            editor.ui.addButton('SuperButton', { // add new button and bind our command
                label: "Click me",
                command: 'mySimpleCommand',
                toolbar: 'insert',
                icon: '/fileupload.png'
            });
            /* Add button */
            CKEDITOR.instances['editor1']
            .on('change', () => {
                console.log('Change');
            })
        }
    },
    methods: {
        InsertHtml(html) {//good
            CKEDITOR.instances['editor1'].insertHtml(html);
        },
        onTest() {
            this.InsertHtml(`<img src="/images2/734032c1-d157-43ba-a4d7-38e9c031c305_shark.jpg" />`);
            //D:\NetCore2023_8\NetCore3VueStarterTool_Cke_Tree\wwwroot/images2/734032c1-d157-43ba-a4d7-38e9c031c305_shark.jpg
        }
    }
}
</script>
